* {
  box-sizing: border-box;
}

body {
  background-color: #eee;
}

.loader {
  color: #000;
  margin: 20% 50%;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 68px;
  font-weight: 400;
  display: inline-block;
  position: relative;
  transform: translateX(-50%);
}

.loader:after {
  content: "Loading";
  z-index: 5;
  position: relative;
}

.loader:before {
  content: "";
  height: 6px;
  width: 100%;
  background: linear-gradient(#ff3d00 100%, rgba(0, 0, 0, 0) 0) 0 0 / 0% no-repeat;
  border: 1px solid;
  border-radius: 10px;
  animation: lineGrow 10s linear infinite;
  position: absolute;
  bottom: 0;
}

@keyframes lineGrow {
  to {
    background-size: 100%;
  }
}

.cat-info {
  column-gap: 18px;
  display: flex;
}

.description {
  max-width: 50%;
  font-size: 22px;
  font-weight: 400;
}

